<template>
	<view class="Questions">
		<u-input v-model="value" :type="type" placeholder='请输入问题详细内容' :border="border" :height="height" :auto-height="autoHeight" />
		<view class="">
			<u-upload :file-list="fileList" :action="action" name='files' @on-remove='onRemove' :header='header' max-count="3" @on-error="onErrors" @on-success="onSuccess" @on-uploaded="onUploaded" upload-text='上传图片'></u-upload>
		</view>
		<view class="btn" @click="submit">
			<button style="background-color: #86B0D4;color: #FFFFFF;" type="default">提交</button>
		</view>
	</view>
</template>

<script>
	import {orderQuestionSave,delOrderPicture} from '@/utill/api/order/order.js'
	export default{
		data(){
			return{
				value: '',
				type: 'textarea',
				border: true,
				height: 200,
				autoHeight: true,
				action:this.$uploadUrl+'/cosFile/uploadImages',
				header:{
					 "token":uni.getStorageSync('token')
				},
				fileList:[],
				pictrueId:[],
				orderNo:'',
			}
		},
		methods:{
			// 回调参数中的lists参数，为目前组件内的所有图片数组，index为当前操作的图片的索引，name为通过props传递的index参数
			onRemove(index, lists, name){
				// console.log(index)
				// console.log(lists)
				// console.log(this.pictrueId)
				// console.log(this.pictrueId[index])
				// console.log(this.pictrueId[index].toString())
				delOrderPicture({
					id:this.pictrueId[index].toString()
				}).then(res=>{
					console.log(res)
					if(res.data.code!=0){
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
				this.pictrueId=[]
				lists.forEach(val=>{
					console.log(val)
					this.pictrueId.push(val.response.object[0]) 
				})
				console.log(this.pictrueId)
			},
			onErrors(res, index, lists, name){
				console.log(res)
				uni.showToast({
					title:res.data.msg
				})
			},
			onSuccess(data, index, lists, name){
				// data为服务器返回的数据，name为通过props传递的index参数
				console.log(data)
				console.log(index)
				console.log(lists)
			},
			onUploaded(lists, name){//所有图片上传完毕触发
				// 可以通过此事件，将lists参数获取，提交给后端使用，name为通过props传递的index参数
				console.log(lists)
				this.pictrueId=[]
				lists.forEach(val=>{
					console.log(val)
					this.pictrueId.push(val.response.object[0]) 
				})
				console.log(this.pictrueId)
			},
			submit(){
				console.log(this.pictrueId)
				console.log(this.pictrueId.toString())
				console.log(this.value)
				console.log(this.orderNo)
				if(!this.value){
					uni.showToast({
						title:'问题不能为空',
						icon:'none'
					})
					return;
				}
				orderQuestionSave({
					description:this.value,
					picture:this.pictrueId.toString(),//字符串形式
					tagNumber:this.orderNo
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						uni.showToast({
							title:res.data.msg
						})
						uni.navigateBack({//提交成功，返回详情页面
							delta:1
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			}
		},
		onLoad(options) {
			console.log(options.orderNo)
			this.orderNo=options.orderNo
		}
	}
</script>

<style lang="scss">
	page{
		background-color: #FFFFFF;
	}
	.Questions{
		padding: 0 30rpx;
	}
	.btn{
		width: 100%;
		position: fixed;
		bottom: 20rpx;
		left: 0;
		right: 0;
		padding: 0 30rpx;
	}
</style>
